import React, { useEffect, useState } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import DF from '../data/DF.js'
import { Paper, Typography, Card, Box, Grid, Grow, Collapse } from '@material-ui/core'



const useStyles = makeStyles(theme => ({
  root: {
    flexGrow: 1,
    backgroundColor:"#c4c4c4",
    padding:"1%",
  },
  paper: {
    padding: theme.spacing(2),
    textAlign: 'center',
    color: theme.palette.text.secondary,
    
  },
  large: {
    width: theme.spacing(20),
    height: theme.spacing(20),
    display:'inline-flex',
  },
  card: {
    maxWidth: 345,
  },
  media: {
    height: 140,
  },
  
}));

const Tecnologies = ({tecnology}) => {

  return(
    <Grow timeout={400} in={true}>
      <Paper>
        <Typography variant="h4" align="center">{tecnology.tittle}</Typography>
      </Paper>
    </Grow>
  );

}

export default Tecnologies
