import React, { useEffect, useState } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Paper from '@material-ui/core/Paper';
import Grid from '@material-ui/core/Grid';
import Card from '@material-ui/core/Card';
import CardActionArea from '@material-ui/core/CardActionArea';
import CardActions from '@material-ui/core/CardActions';
import CardContent from '@material-ui/core/CardContent';
import CardMedia from '@material-ui/core/CardMedia';
import { Button, Typography } from '@material-ui/core';
import Tecnologies from './content-pages/Tecnologies';
import DF from './data/DF';
import Resume from './content-pages/Resume'
import Studies from './content-pages/Studies'


const useStyles = makeStyles(theme => ({
  root: {
    flexGrow: 1,
    backgroundColor:"#c4c4c4",
    padding:"1%",
  },
  paper: {
    padding: theme.spacing(2),
    textAlign: 'center',
    color: theme.palette.text.secondary,
    
  },
  large: {
    width: theme.spacing(20),
    height: theme.spacing(20),
    display:'inline-flex',
  },
  card: {
    maxWidth: 345,
  },
  media: {
    height: 140,
  },
  
}));
let isTecnology 
const RightPart = ({componentShow}) => {
  const [componentBited,setComponentBited]= useState("resume")
  const classes = useStyles();
  useEffect(() => {
    switch (componentShow) {
      case 'html5':
        isTecnology = true
        setComponentBited(DF.html5)
        break;
      case 'css3':
        isTecnology = true
        setComponentBited(DF.css3)
        break;
      case 'javascript':
        isTecnology = true
        setComponentBited(DF.javascript)
        break;
      case 'react':
        isTecnology = true
        setComponentBited(DF.react)
        break;
      case 'git':
        isTecnology = true
        setComponentBited(DF.git)
        break;
      case 'vuejs':
        isTecnology = true
        setComponentBited(DF.vue)
        break;
      case 'nodejs':
        isTecnology = true
        setComponentBited(DF.node)
        break;
      case 'mongodb':
        isTecnology = true
        setComponentBited(DF.mongo)
        break;
      case 'resume':
        isTecnology = false
        setComponentBited('resume')
        break;
      case 'studies':
        isTecnology = false
        setComponentBited('studies')
        break;   
      case 'experience':
        isTecnology = false
        setComponentBited('resume')
        break;    
      case 'contact':
        isTecnology = false
        setComponentBited('resume')
      default:
        break;
    }
  
  });
  
  
  return(
  <Grid item xs={12} md={9}>
    {isTecnology ? <Tecnologies tecnology={componentBited}/> : "" }
    {componentBited=="resume"   ? <Resume /> : ""}
    {componentBited=="studies"   ? <Studies /> : ""}
  </Grid>
  );

}

export default RightPart
