import React from 'react';
import './App.css';
import { makeStyles } from '@material-ui/core/styles';
import Paper from '@material-ui/core/Paper';
import Grid from '@material-ui/core/Grid';
import Card from '@material-ui/core/Card';
import CardActionArea from '@material-ui/core/CardActionArea';
import CardActions from '@material-ui/core/CardActions';
import CardContent from '@material-ui/core/CardContent';
import CardMedia from '@material-ui/core/CardMedia';
import { Button, Typography } from '@material-ui/core';
import RightPart from './components/RightPart.js'
import LeftPart from './components/LeftPart';


const useStyles = makeStyles(theme => ({
  root: {
    flexGrow: 1,
    backgroundColor:"#c4c4c4",
    padding:"1%",
    margin: "0 auto 35px auto"
  },
  paper: {
    padding: theme.spacing(2),
    textAlign: 'center',
    color: theme.palette.text.secondary,
    
  },
  large: {
    width: theme.spacing(20),
    height: theme.spacing(20),
    display:'inline-flex',
  },
  card: {
    maxWidth: 345,
  },
  media: {
    height: 140,
  },
  
}));


const App = () => {
  const [componentShow, setComponentShow] = React.useState()
  const classes = useStyles();
  const componentSet = componentToShow =>{
    setComponentShow(componentToShow)
  }
  return (
    <div className={classes.root}>
      <Grid container spacing={1}>
        <LeftPart handler={componentSet} />
        <RightPart componentShow={componentShow}/>
      </Grid>
    </div>
  );
}

export default App;
